.App{text-align:center;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#f2fff9;height:100vh}.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear;height:40vmin;pointer-events:none}.App-header{background-color:#282c34;min-height:100vh;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes App-logo-spin{from{-webkit-transform:rotate(0deg);transform:rotate(0deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.login{width:25em;height:39.4em;display:-webkit-flex;display:flex;margin-left:50px;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;margin-top:100px;position:relative;z-index:99}.login .container{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;background-color:#fff;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.2);border-radius:4px;position:relative;z-index:99;width:100%;height:80%;z-index:99;padding:17px 10px}.login .right-side{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;height:70%;background-color:#3498db;width:100%;position:absolute;right:-34%;border-radius:6px;z-index:1;transition:all 400ms ease-in-out;cursor:pointer;box-shadow:0px 0px 12px 2px rgba(15,15,15,0.281)}.login .right-side.right{font-weight:700;font-weight:700;right:-37%;-webkit-align-items:flex-end;align-items:flex-end}.login .right-side.right:hover{right:-42%}.login .right-side.left{font-weight:700;right:35%;-webkit-align-items:flex-start;align-items:flex-start}.login .right-side.left:hover{right:40%}.login .right-side .text{font-size:21px;font-weight:500;color:#fff;margin-right:3em;margin-left:3em}.btn{font-family:"Poppins", sans-serif;font-weight:900;font-size:18px;padding:5px 20px;border:0;margin-bottom:65px;background-color:#3498db;color:#fff;border-radius:20px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2386c8}.btn:focus{outline:none}.newpic{width:35em;margin-right:100px;padding-top:50px}.newpic img{width:100%;height:100%}

.base-container{width:100%;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;font-weight:600}.base-container .header{font-size:25px;font-family:"Poppins", sans-serif;font-weight:900;color:#3498db}.base-container .content{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.base-container .image{width:18em;margin-left:40px;padding-top:50px}.base-container .image img{width:100%;height:100%}.base-container .form-group{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.base-container .form-group lable{font-size:20px}.base-container .form-group input{margin-top:3px;min-width:18em;height:40px;padding:0px 10px;padding-left:20px;font-size:16px;font-family:"Poppins", sans-serif;background-color:#f3f3f3;border:0;border-radius:30px;margin-bottom:31px;transition:all 250ms ease-in-out;font-weight:500}.base-container .form-group input:focus{outline:none;box-shadow:0px 0px 12px 0.8px #3498db}.base-container .form{margin-top:1em;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}.btn{font-size:21px;padding:5px 20px;border:0;background-color:#1285d1;color:#fff;border-radius:30px;transition:all 250ms ease-in-out;cursor:pointer}.btn:hover{background-color:#2f00ff}.btn:focus{outline:none}

